@import "./default.value.scss";


// Media Query Ranges
$small-screen-up: 601px !default;
$medium-screen-up: 993px !default;
$large-screen-up: 1201px !default;
$small-screen: 600px !default;
$medium-screen: 992px !default;
$large-screen: 1200px !default;

$medium-and-up: "only screen and (min-width : #{$small-screen-up})" !default;
$large-and-up: "only screen and (min-width : #{$medium-screen-up})" !default;
$extra-large-and-up: "only screen and (min-width : #{$large-screen-up})" !default;
$small-and-down: "only screen and (max-width : #{$small-screen})" !default;
$medium-and-down: "only screen and (max-width : #{$medium-screen})" !default;
$medium-only: "only screen and (min-width : #{$small-screen-up}) and (max-width : #{$medium-screen})" !default;

.modal {
  &:focus {
    outline: none;
  }

  box-shadow: 0 24px 38px 3px rgba(0, 0, 0, 0.14),
  0 9px 46px 8px rgba(0, 0, 0, 0.12),
  0 11px 15px -7px rgba(0, 0, 0, 0.2);

  display: none;
  position: fixed;
  left: 0;
  right: 0;
  background-color: #fafafa;
  padding: 0;
  max-height: 50%;
  width: 55%;
  margin: auto;
  overflow-y: auto;

  border-radius: 5px;
  will-change: top, opacity;


  @media #{$medium-and-down} {
    width: 80%;
  }

  h1, h2, h3, h4 {
    margin-top: 0;
  }

  .modal-content {
    padding: 24px;

    & h5 {
      font-size: 1.2rem;
      margin: .5rem 0;
    }
  }

  .modal-close {
    cursor: pointer;
  }

  .modal-footer {
    border-radius: 0 0 2px 2px;
    background-color: #fafafa;
    padding: 4px 6px;
    height: 48px;
    width: 100%;
    text-align: right;

    .btn, .btn-flat {
      margin: 6px 0;
    }
  }
}

.modal-overlay {
  position: fixed;
  z-index: 999;
  top: -25%;
  left: 0;
  bottom: 0;
  right: 0;
  height: 125%;
  width: 100%;
  background: #000;
  display: none;

  will-change: opacity;
}

// Modal with fixed action footer
.modal.modal-fixed-footer {
  padding: 0;
  height: 70%;

  .modal-content {
    position: absolute;
    height: calc(100% - 56px);
    max-height: 100%;
    width: 100%;
    overflow-y: auto;

  }

  .modal-footer {
    border-top: 1px solid rgba(0, 0, 0, .1);
    position: absolute;
    bottom: 0;


  }
}

// Modal Bottom Sheet Style
.modal.bottom-sheet {
  top: auto;
  bottom: -100%;
  margin: 0;
  width: 100%;
  max-height: 45%;
  border-radius: 0;
  will-change: bottom, opacity;
}


@mixin modalFooter {
  padding: 0;
  display: flex;
  justify-content: flex-end;
  align-items: center;

  & .btn-flat {
    padding: .2rem 1rem;
    margin-right: 1.5rem;
    border-radius: 5px;

    &:hover {
      background-color: rgba(0, 0, 0, 0.28);
      color: #fff;
    }
  }
}

.modal {
  overflow: hidden;

  &.Error.open {
    background-color: $colorModalBodyError;
    color: $colorTextModalError;

    .modal-footer {
      background-color: $colorModalFooterError;
      @include modalFooter;
    }
  }

  &.Confirmation.open {
    background-color: $colorModalBodyConfirmation;
    color: $colorTextModalConfirmation;

    .modal-footer {
      background-color: $colorModalFooterConfirmation;
      @include modalFooter;
    }

  }

  &.Information.open {
    background-color: $colorModalBodyInformation;
    color: $colorTextModalInformation;

    .modal-footer {
      background-color: $colorModalFooterInformation;
      @include modalFooter;

    }

  }

}



