::-webkit-scrollbar {
  width: 12px;
  background-color: #000000;
}

::-webkit-scrollbar-thumb {
  border-width: 1px 1px 1px 2px;
  border-color: #777;
  background-color: #667c65;
}

::-webkit-scrollbar-thumb:hover {
  border-width: 1px 1px 1px 2px;
  border-color: #555;
  background-color: #637744;
}

::-webkit-scrollbar-track {
  border-width: 0;
}

::-webkit-scrollbar-track:hover {
  border-left: solid 1px #aaa;
  background-color: #000000;
}

#root {

}

.btn {
  text-decoration: none;
  cursor: pointer;
  border: none;
  text-align: center;
}

.container {
  padding: 2rem;
}

.main-container {

  & .up-arrow {
    position: fixed;
    font-size: 1.65rem;
    color: #c0aa83;
    font-weight: 700;
    right: 2rem;
    bottom: 2rem;
    height: 45px;
    width: 45px;
    border-radius: 50%;
    border: 2px solid #c0aa83;
    transition: all .5s ease-in-out;
    cursor: pointer;
    z-index: 200;
    text-align: center;

    &:hover {
      border-color: #fff;
      color: #fff;
      background-color: rgba(192, 170, 131, 0.5);
      transition: all .5s ease-in-out;
    }
  }

  .btn-view-all-products {
    display: inline-block;
    margin-top: 2rem;
    padding: .5rem 1rem;
    font-weight: 700;
    color: #c0aa83;
    font-size: 1rem;
    border-radius: .25rem;
    border: 1px solid #c0aa83;
    cursor: pointer;
    transition: color 200ms ease-in-out, border 200ms ease-in-out, background-color 200ms ease-in-out;

    &:hover {
      background-color: #c0aa83;
      color: #000;
      transition: color 200ms ease-in-out, border 200ms ease-in-out, background-color 200ms ease-in-out;
    }
  }


}